	module.exports = {"data":{"markdownRemark":{"html":"<h2>Background Color Property</h2>\n<p>You use the <code>background-color</code> property to set the background color of an element. You can either use a color value (color name, hexadecimal value, RGB/RGBA value, HSL/HSLA value) or the keyword <code>transparent</code>. </p>\n<p><strong>Example:</strong></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">body</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token property\">background-color</span><span class=\"token punctuation\">:</span> crimson<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token selector\">div</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token property\">background-color</span><span class=\"token punctuation\">:</span> #ffffff<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token selector\">.myClass</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token property\">background-color</span><span class=\"token punctuation\">:</span> <span class=\"token function\">rgba</span><span class=\"token punctuation\">(</span>0, 0, 0, 0.5<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h4>Property Values:</h4>\n<p><code>background-color: color | transparent | initial | inherit;</code></p>\n<p><code>color</code> - Specifies the background color (color name, hexadecimal value, RGB/RGBA value, HSL/HSLA value). </p>\n<p><code>transparent</code> - The default value. Sets the background color as transparent.</p>\n<p><code>initial</code> - Sets this property to its initial value (the default value).</p>\n<p><code>inherit</code> - Inherits this property from its parent element.</p>\n<h4>More Information:</h4>\n<p><a href='https://developer.mozilla.org/en-US/docs/Web/CSS/background-color?v=b' target='_blank' rel='nofollow'>MDN web docs</a></p>","fields":{"slug":"/css/properties/background-color-property/"},"frontmatter":{"title":"Background Color Property"}}},"pathContext":{"slug":"/css/properties/background-color-property/"}}

